<script>
import TreeMenu from "@/components/TreeMenu.vue";
import MyFooter from "@/components/MyFooter.vue";
import MyHeader from "@/components/MyHeader.vue";
import home from "@/api/home";
import config from "@/common/config";
import SliderSecond from "@/components/SliderSecond.vue";
import SliderThird from "@/components/SliderThird.vue";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";

library.add(faSpinner);

/*
How to bind img src to data in Vue
https://stackoverflow.com/questions/48847644/how-to-bind-img-src-to-data-in-vue

Vue-cli compile image src via dynamic property #450
https://github.com/vuejs-templates/webpack/issues/450

https://vuejs-templates.github.io/webpack/static.html
https://webpack.js.org/guides/dependency-management/#require-with-expression

bootstrap-vue Component img src resolving
https://bootstrap-vue.org/docs/reference/images
*/

let hotProducts = [
  home.weeklyHot,
  home.monthlyHot,
  home.seasonalHot,
  home.yearlyHot,
];

export default {
  name: "Home",
  data() {
    return {
      slide: 0,
      sliding: null,
      list: config.treeMenu,
      loading: false,
      banner: config.banner,
      bannerDimension: "height:180px;",
      myDimension: "height:500px;max-width:100%;",
      tinyML: require("@/assets/img/TinyML.webp"),
      tabTiles: config.tabTiles,
      hotProducts: hotProducts,
      weeklyHot: home.weeklyHot,
      monthlyHot: home.monthlyHot,
      seasonalHot: home.seasonalHot,
      yearlyHot: home.yearlyHot,
      options: {
        // type: 'slide',
        type: "loop",
        arrows: true,
        // arrows: 'slider',
        autoplay: true,
        interval: 2000,
        trimSpace: true,
        pagination: true,
        rewind: true,
        autoWidth: true,
        //width: 1100,
        perPage: 5,
        perMove: 1,
        gap: "1rem",
        focus: "center",
      },
      slides: home.newBookReleases,
    };
  },
  methods: {
    handleScroll() {
      let scrollTop =
        document.documentElement.scrollTop ||
        window.pageYOffset ||
        document.body.scrollTop;
      console.log(scrollTop);
      console.log(
        "document.documentElement.scrollTop: " +
          document.documentElement.scrollTop
      );
      console.log("window.pageYOffset: " + window.pageYOffset);
      console.log("document.body.scrollTop: " + document.body.scrollTop);

      let windowHeight = window.innerHeight;
      console.log("windowHeight: " + windowHeight);
      console.log("document.body.clientHeight: " + document.body.clientHeight);
      if (scrollTop + windowHeight + 250 >= document.body.clientHeight) {
        this.loading = true;
        console.log("this.loading: " + this.loading);
      } else {
        this.loading = false;
      }
    },
  },
  components: {
    //DropDownMenu,
    MyHeader,
    TreeMenu,
    MyFooter,
    SliderSecond,
    SliderThird,
  },
  mounted() {
    //window.addEventListener("scroll", this.handleScroll, true);
  },
  destroyed() {
    //window.removeEventListener("scroll", this.handleScroll, true);
  },
};
</script>

